@import url(https://fonts.googleapis.com/css2?family=Alata&family=Roboto&display=swap);
:root{--font-size-title: 100px;--font-size-subtitle: 50px;--spacing-large: 70px;--spacing-small: 15px;--nav-height: 100px}@media only screen and (max-width: 560.98px), (max-height: 560.98px){:root{--spacing-small: 10px}}@media only screen and (max-height: 360.98px){:root{--nav-height: 50px}}body{margin:0px;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-content: stretch;
  align-items: stretch;
}
.titleCard{border-radius:0px 0px 30px 30px;height:calc(100vh - var(--nav-height));width:100%}.bgContainer{position:absolute;top:0;overflow:hidden;background-color:black;transform:scaleY(1);z-index:-1}.bg{background-position:top -50px left -50px;position:absolute;left:0;top:0;background-size:cover;background-position:0 0;background-repeat:no-repeat;width:110vw;height:100vh}.content{background:rgba(0,0,0,0.5);box-sizing:border-box;color:white;border-radius:0px 0px 30px 30px;display:grid;grid-template-columns:auto auto;grid-template-rows:1fr 1fr;grid-auto-flow:column;justify-items:end;grid-column-gap:var(--spacing-small);padding:var(--spacing-small);overflow:hidden}.content .subtitle{align-self:end;font-size:6vw;font-family:'Alata', sans-serif}.content .name{font-size:16vw;font-family:'Alata', sans-serif}.content .border{height:100%;grid-column:2 / 2;grid-row:1 / 4;justify-self:center;align-self:center;padding:var(--spacing-small) 0px var(--spacing-small) var(--spacing-small);box-sizing:border-box;overflow:hidden;display:flex;flex-direction:column;justify-content:center;align-content:stretch;align-items:center}.content .border .helper{display:inline-block;height:100%;vertical-align:middle}.content .border img{max-width:90%;max-height:90%;object-fit:contain;vertical-align:middle}.content .border div{padding:10px;text-align:center;font-size:2vw}.content .border div a:visited{color:white}.content .border div a:link{color:white}.content .border div a:hover{color:grey}@media (max-aspect-ratio: 2 / 3){.content{grid-template-columns:auto;grid-template-rows:1fr 1fr auto;grid-auto-flow:row;justify-items:center}.content .subtitle{font-size:8vw}.content .name{font-size:14vw}.content .border{height:100%;grid-column:auto / auto;grid-row:auto / auto}.content .border div{padding:5px;text-align:center;font-size:4vh}}@media (min-aspect-ratio: 70 / 27){.content .name{font-size:26vh}.content .subtitle{font-size:16vh}}

